@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Light.woff2") format("woff2"), url("../font/Rubik-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Regular.woff2") format("woff2"), url("../font/Rubik-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Medium.woff2") format("woff2"), url("../font/Rubik-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Bold.woff2") format("woff2"), url("../font/Rubik-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote {
  margin: 0;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

textarea {
  resize: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a,
input,
button {
  outline: 0;
  border: 0;
}

button {
  background-color: transparent;
  cursor: pointer;
}

/*---------------------------------*/

::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar {
  height: 3px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #ff6838;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Rubik", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: #31352b;
  background-color: #f9faff;
}

.logo {
  display: inline-block;
  max-width: 200px;
  height: 39px;
  border-radius: 5px;
  transition: opacity 0.3s, box-shadow 0.3s ease-in-out;
}

.logo__img {
  width: 100%;
}

.logo:hover {
  opacity: 0.8;
}

.logo:focus {
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.logo:active {
  box-shadow: 0px 0px 0px 2px rgba(255, 103, 56, 0);
  opacity: 0.5;
}

.link {
  border-radius: 5px;
  transition: color 0.3s, box-shadow 0.3s, text-shadow 0.3s ease-in-out;
}

.link:hover {
  color: #ff6838;
}

.link:focus {
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.link:active {
  box-shadow: 0px 0px 0px 2px rgba(255, 103, 56, 0);
  text-shadow: 2px 2px 5px rgba(49, 53, 43, 0.5019607843);
}

.link--line {
  color: #363853;
  position: relative;
  border-radius: 5px;
}

.link--line:hover::after,
.link--line:focus::after,
.link--line:active::after {
  opacity: 0;
}

.link--line::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  background-color: #363853;
  transition: opacity 0.3s ease-in-out;
}

.hero__title {
  font-family: "Rubik", sans-serif;
  font-size: 66px;
  font-weight: 500;
  line-height: 1.3;
  color: #363853;
  text-transform: uppercase;
}

.title {
  font-weight: 500;
  line-height: 1.3;
  font-size: 36px;
  text-align: center;
  color: #363853;
}

.title span {
  display: block;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #fff;
  border-radius: 5px;
  background-color: #ff6838;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.btn-bg:hover {
  color: #ff6838;
  background-color: rgba(255, 255, 255, 0.1019607843);
  box-shadow: 0px 5px 12px rgba(255, 104, 56, 0.2), 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:focus {
  color: #ff6838;
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:active {
  color: #ff6838;
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0px 0px 0px 2px transparent;
}

.popular,
.restaurant,
.apps {
  padding: 120px 0;
}

body.header-fixed header {
  position: fixed;
  top: 0;
  right: 0;
  padding-top: 0;
  width: 100%;
  z-index: 2;
}

/*---------------------------------*/

.header {
  position: fixed;
  padding-top: 57px;
  width: 100%;
  z-index: 100;
  background-color: #f9faff;
  transition: 0.3s all;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .logo {
  padding-left: 5px;
}

.navbar__inner {
  display: flex;
  align-items: center;
}

.menu {
  width: 301px;
  display: flex;
  justify-content: space-between;
  margin-right: 50px;
}

.menu__link {
  font-size: 18px;
}

.menu__link--active {
  color: #ff6838;
}

.buttons {
  display: flex;
}

.buttons__item:nth-child(1) {
  margin-right: 10px;
}

.buttons__btn {
  width: 44px;
  height: 44px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.buttons__btn:hover {
  background-color: #ff6838;
}

.buttons__btn:hover .buttons__icon {
  stroke: #fff;
}

.buttons__btn:hover .buttons__count {
  background-color: #fff;
  color: #ff6838;
}

.buttons__btn:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.buttons__btn:focus .buttons__icon {
  stroke: #363853;
}

.buttons__btn:focus .buttons__count {
  background-color: #ff6838;
  color: #fff;
}

.buttons__btn:active {
  box-shadow: 0px 0px 0px 2px rgba(255, 103, 56, 0);
  background-color: #fff2ed;
}

.buttons__btn:active .buttons__icon {
  stroke: #363853;
}

.buttons__icon {
  stroke: #363853;
  transition: stroke 0.3s ease-in-out;
}

.buttons__count {
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  font-size: 10px;
  text-align: center;
  color: #ffffff;
  background-color: #ff6838;
  top: 7px;
  right: 3px;
  transition: color 0.3s, background-color 0.3s ease-in-out;
}

.burger {
  width: 43px;
  height: 43px;
  border-radius: 5px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 2px;
  margin-left: 10px;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
  display: none;
}

.burger:hover {
  background-color: #ff6838;
}

.burger:hover .burger__line {
  background-color: #fff;
}

.burger:hover .burger__line::before,
.burger:hover .burger__line::after {
  background-color: #fff;
}

.burger:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.burger:focus .burger__line {
  background-color: #363853;
}

.burger:focus .burger__line::before,
.burger:focus .burger__line::after {
  background-color: #363853;
}

.burger:active {
  box-shadow: 0px 0px 0px 2px rgba(255, 103, 56, 0);
  background-color: #fff2ed;
}

.burger__line {
  width: 30px;
  height: 2px;
  background-color: #363853;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: background-color 0.3s ease-in-out;
}

.burger__line::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  background-color: #363853;
  top: -10px;
  left: 0;
  transition: background-color 0.3s ease-in-out;
}

.burger__line::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  background-color: #363853;
  top: 10px;
  left: 0;
  transition: background-color 0.3s ease-in-out;
}

.hero {
  padding-top: 159px;
}

.hero__inner {
  background-image: url(../img/decore/scooter-guy.png);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: contain;
  height: 660px;
  position: relative;
}

.hero__content {
  max-width: 450px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.hero__title {
  margin-bottom: 20px;
}

.hero__title-span {
  display: block;
  color: #ff6838;
}

.hero__text {
  margin-bottom: 20px;
}

.hero__button {
  font-size: 18px;
  margin-right: 34px;
  padding: 13px 32px;
}

.hero__button:hover {
  background-color: #fff;
}

.popular {
  text-align: center;
}

.popular__title {
  margin-bottom: 60px;
}

.popular__filter {
  display: flex;
  overflow: auto;
  padding: 2px;
  margin-bottom: 45px;
}

.popular__item:not(:last-child) {
  margin-right: 20px;
}

.popular__button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #363853;
  padding: 0;
  white-space: nowrap;
  border-radius: 5px;
  padding: 13px 32px;
  box-shadow: 0px 0px 0px 1px #ececec;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.popular__button:hover {
  color: #ff6838;
  background-color: rgba(255, 122, 80, 0.1);
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.popular__button:hover .popular__icon {
  fill: #ff6838;
}

.popular__button:focus {
  color: #363853;
  background-color: #fff;
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.popular__button:focus .popular__icon {
  fill: #363853;
}

.popular__button:active {
  color: #363853;
  box-shadow: inset 0px -5px 12px rgba(255, 104, 56, 0.2);
}

.popular__button:active .popular__icon {
  fill: #363853;
}

.popular__button--active {
  color: #fff;
  background-color: #ff6838;
}

.popular__button--active .popular__icon {
  fill: #fff;
}

.popular__icon {
  width: 44px;
  height: 44px;
  fill: #363853;
  margin-right: 10px;
  transition: fill 0.3s ease-in-out;
}

.popular__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  line-height: 1;
  padding: 0;
}

.popular-food {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 28px;
  margin-bottom: 60px;
}

.popular-food__card {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 0px 1px #ececec;
  transition: box-shadow 0.3s ease-in-out;
}

.popular-food__card:hover {
  box-shadow: 0px 0px 0px 1px #ff6838;
}

.popular-food__product {
  min-height: 299px;
  height: 100%;
  padding: 20px 9px 22px;
  display: flex;
  flex-direction: column;
}

.popular-food__link {
  flex-grow: 1;
}

.popular-food__img {
  width: 140px;
  height: 140px;
  margin: 0 auto;
  object-fit: contain;
  margin-bottom: 10px;
}

.popular-food__alt {
  padding: 0;
  word-spacing: 1px;
  font-weight: 400;
  font-size: 15px;
  line-height: 1;
}

.popular-food__price {
  font-weight: 500;
  font-size: 15px;
  line-height: 1.4;
  margin-bottom: 9px;
}

.popular-food__button {
  margin: 0 auto;
  padding: 6px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
}

.mixitup-control-active {
  color: #fff;
  background-color: #ff6838;
}

.mixitup-control-active .popular__icon {
  fill: #fff;
}

.delivery__inner {
  max-width: 1030px;
  background-image: url(../img/decore/food-delivery.png);
  background-repeat: no-repeat;
  background-position: left center;
  background-size: contain;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
}

.delivery__content {
  padding-top: 59px;
  max-width: 454px;
  margin-right: 15px;
}

.delivery__title {
  text-align-last: left;
  margin-bottom: 30px;
}

.delivery__text {
  word-spacing: -1px;
  margin-bottom: 30px;
}

.delivery__list {
  margin-bottom: 28px;
}

.delivery__item {
  font-size: 24px;
  color: #363853;
  padding-left: 66px;
}

.delivery__item:nth-child(1) {
  background-image: url(../img/svg/icon-time.svg);
  background-repeat: no-repeat;
}

.delivery__item:nth-child(2) {
  background-image: url(../img/svg/icon-delivery.svg);
  background-repeat: no-repeat;
}

.delivery__item:nth-child(3) {
  background-image: url(../img/svg/icon-resturent.svg);
  background-repeat: no-repeat;
}

.delivery__item:not(:last-child) {
  margin-bottom: 20px;
}

.delivery__button {
  font-size: 18px;
  line-height: 1.3;
  padding: 12px 33px;
  margin-bottom: 55px;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir=rtl] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.restaurant__title {
  padding-right: 18px;
  padding-top: 3px;
  line-height: 1.3;
  margin-bottom: 45px;
}

.restaurant__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  grid-gap: 31px;
}

.restaurant__card {
  max-width: 370px;
  border-radius: 10px;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1019607843);
  background-color: #fff;
  transition: box-shadow 0.3s ease-in-out;
  position: relative;
}

.restaurant__card:hover {
  box-shadow: 0px 3px 15px rgba(255, 104, 56, 0.5019607843);
}

.restaurant__img {
  width: 370px;
  height: 300px;
  border-radius: 10px 10px 0px 0px;
  object-fit: cover;
}

.restaurant__time {
  padding-left: 32px;
  background-image: url(../img/svg/icon-timer.svg);
  background-repeat: no-repeat;
  background-position: left;
  background-size: contain;
}

.restaurant__link {
  position: absolute;
  right: 20px;
  bottom: 19px;
  padding: 4px 27px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.restaurant__link:hover .restaurant__icon {
  fill: #ff6838;
  transform: translateX(17px);
}

.restaurant__link:focus .restaurant__icon {
  fill: #ff6838;
}

.restaurant__link:active .restaurant__icon {
  fill: #ff6838;
}

.restaurant__icon {
  width: 24px;
  height: 24px;
  fill: white;
  transition: fill 0.3s, transform 0.3s ease-in-out;
}

.restaurant__info {
  padding: 15px 20px 20px;
}

.restaurant__name {
  font-weight: 400;
  font-size: 24px;
  line-height: 1.3;
  margin-bottom: 20px;
}

.restaurant__time {
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  opacity: 0.9;
}

.restaurant .slick-dots {
  max-width: 165px;
  height: 15px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.restaurant .slick-dots li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.restaurant .slick-dots li:not(:last-child) {
  margin-right: 15px;
}

.restaurant .slick-dots li button {
  font-size: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #c2c2c2;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.restaurant .slick-dots li button:hover {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px rgba(255, 122, 80, 0.7019607843);
}

.restaurant .slick-dots li button:focus {
  background-color: #c2c2c2;
  box-shadow: 0px 0px 0px 2px #ff6838;
}

.restaurant .slick-dots li button:active {
  background-color: #ffae94;
  box-shadow: none;
}

.restaurant .slick-dots .slick-active button {
  background-color: #ff6838;
}

