.reviews__title {
  padding-top: 5px;
  margin-bottom: 43px;
}

.reviews-slider__item {
  max-width: 991px;
  display: flex !important;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 49px;
}

.reviews-slider__photo {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 40px;
}

.reviews-slider__quotes {
  max-width: 650px;
  padding-left: 39px;
}

.reviews-slider__comment {
  font-style: italic;
  margin-bottom: 30px;
  padding-right: 40px;
  position: relative;
}

.reviews-slider__comment::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 20px;
  background-image: url(../img/svg/quotes.svg);
  left: -39px;
}

.reviews-slider__comment::after {
  position: absolute;
  content: "";
  width: 24px;
  height: 20px;
  background-image: url(../img/svg/quotes.svg);
  right: 0;
  bottom: -3px;
  transform: rotateY(180deg);
}

.reviews-slider__name {
  font-weight: 500;
  line-height: 1.3;
  color: #ff6838;
}

.reviews-slider__prof {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #31352b;
  display: block;
  margin-top: 15px;
}

.reviews-slider .slick-dots {
  max-width: 219px;
  height: 34px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.reviews-slider .slick-dots li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.reviews-slider .slick-dots li:not(:last-child) {
  margin-right: 15px;
}

.reviews-slider .slick-dots li button {
  font-size: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #c2c2c2;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.reviews-slider .slick-dots li button:hover {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px rgba(255, 122, 80, 0.7019607843);
}

.reviews-slider .slick-dots li button:focus {
  background-color: #c2c2c2;
  box-shadow: 0px 0px 0px 2px #ff6838;
}

.reviews-slider .slick-dots li button:active {
  background-color: #ffae94;
  box-shadow: none;
}

.reviews-slider .slick-dots .slick-active button {
  background-color: #ff6838;
}

.reviews-slider .slick-arrow {
  position: absolute;
  bottom: 0;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #c2c2c2;
  transition: box-shadow 0.3s, border 0.3s, background-color 0.3s ease-in-out;
}

.reviews-slider .slick-arrow:hover {
  background-color: #fff2ed;
  border: 2px solid #ffae94;
}

.reviews-slider .slick-arrow:focus {
  border: 2px solid #c2c2c2;
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
  background-color: #fff;
}

.reviews-slider .slick-arrow:active {
  border: 2px solid transparent;
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px transparent;
}

.reviews-slider .slick-arrow__prev {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  left: 50%;
  transform: translateX(-325%);
  z-index: 1;
}

.reviews-slider .slick-arrow__prev:hover .slick-arrow__icon {
  fill: #ffae94;
  transform: matrix(0, 1, 1, 0, 0, 0);
}

.reviews-slider .slick-arrow__prev:focus .slick-arrow__icon {
  fill: #c2c2c2;
  transform: matrix(0, 1, 1, 0, 0, 0);
}

.reviews-slider .slick-arrow__prev:active .slick-arrow__icon {
  fill: #ff6838;
  transform: matrix(0, 1, 1, 0, 0, 0);
}

.reviews-slider .slick-arrow__next {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  right: 50%;
  transform: translateX(325%);
}

.reviews-slider .slick-arrow__next .slick-arrow__icon {
  transform: rotateY(180deg);
}

.reviews-slider .slick-arrow__next:hover .slick-arrow__icon {
  fill: #ffae94;
  transform: matrix(0, -1, -1, 0, 0, 0);
}

.reviews-slider .slick-arrow__next:focus .slick-arrow__icon {
  fill: #c2c2c2;
  transform: matrix(0, -1, -1, 0, 0, 0);
}

.reviews-slider .slick-arrow__next:active .slick-arrow__icon {
  fill: #ff6838;
  transform: matrix(0, -1, -1, 0, 0, 0);
}

.reviews-slider .slick-arrow__icon {
  width: 34px;
  height: 34px;
  fill: #c2c2c2;
  transition: transform 0.3s, fill 0.3s, stroke 0.3s ease-in-out;
}

.apps {
  padding-bottom: 117px;
}

.apps__inner {
  min-height: 385px;
  display: flex;
  align-items: center;
  background-image: url(../img/decore/app-bg.jpg);
  background-repeat: no-repeat;
  background-position: center right;
}

.apps__content {
  max-width: 540px;
}

.apps__title {
  text-align: left;
  margin-bottom: 45px;
}

.apps__text {
  margin-bottom: 30px;
}

.apps__list {
  display: flex;
}

.apps__item {
  height: 40px;
}

.apps__item:nth-child(1) .apps__link {
  display: inline-block;
  width: 120px;
  height: 40px;
  margin-right: 30px;
  background-image: url(../img/decore/app-store.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.apps__item:nth-child(2) .apps__link {
  display: inline-block;
  width: 135px;
  height: 40px;
  background-image: url(../img/decore/google-play.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.footer__top {
  display: grid;
  grid-template-columns: 200px 153px 89px 187px 261px;
  justify-content: space-between;
  padding-bottom: 57px;
}

.footer__inner {
  max-width: 200px;
}

.footer__form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.footer__input {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #363853;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0px 0px 0px 1px #c2c2c2;
  padding: 13px 5px 13px 15px;
  margin-bottom: 15px;
  transition: color 0.3s, box-shadow 0.3s ease-in-out;
}

.footer__input::placeholder {
  color: #c2c2c2;
  transition: color 0.3s ease-in-out;
}

.footer__input:hover::placeholder {
  color: #363853;
}

.footer__input:focus {
  box-shadow: 0px 0px 0px 2px #ff6838;
}

.footer__input:active::placeholder {
  color: #c2c2c2;
}

.footer__button {
  padding: 13px 0;
  font-size: 14px;
  line-height: 1.3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__button:hover .footer__icon {
  fill: #ff6838;
}

.footer__button:focus .footer__icon {
  fill: #ff6838;
}

.footer__icon {
  width: 16.51px;
  height: 15.6px;
  fill: #fff;
  margin-right: 15px;
  transition: fill 0.3s ease-in-out;
}

.footer__item {
  margin-bottom: 12px;
}

.footer__link {
  white-space: nowrap;
  font-weight: 400;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 0.01em;
  display: inline-flex;
  align-items: flex-start;
}

.footer__copy {
  border-top: 1px solid rgba(194, 194, 194, 0.4941176471);
  padding: 32px 0;
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
}

.address {
  font-style: normal;
}

.address__item {
  margin-bottom: 11px;
}

.address__link {
  height: 32px;
  display: inline-flex;
  justify-content: start;
  align-items: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
}

.address__link:hover .address__icon {
  stroke: #ff6838;
}

.address__link:active .address__icon {
  filter: drop-shadow(2px 2px 5px rgba(49, 53, 43, 0.5019607843));
}

.address__icon {
  width: 24px;
  height: 24px;
  stroke: #31352b;
  margin-right: 7px;
  transition: stroke 0.3s ease-in-out;
}

.social__list {
  display: flex;
}

.social__item:not(:last-child) {
  margin-right: 13px;
}

.social__item:nth-child(1) .social__icon {
  width: 9.98px;
  height: 20.53px;
}

.social__item:nth-child(2) .social__icon {
  width: 20.53px;
  height: 16.68px;
}

.social__item:nth-child(3) .social__icon {
  width: 19.5px;
  height: 19.5px;
}

.social__link {
  width: 39px;
  height: 39px;
  background-color: #ececec;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.social__link:hover {
  background-color: #ff6838;
}

.social__link:hover .social__icon {
  fill: #fff;
}

.social__link:focus {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.social__link:focus .social__icon {
  fill: #31352b;
}

.social__link:active {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px transparent;
}

.social__link:active .social__icon {
  fill: #ff6838;
}

.social__icon {
  fill: #31352b;
  transition: fill 0.3s ease-in-out;
}

@media (max-width: 1130px) {
  .popular-food {
    grid-template-columns: repeat(4, 1fr);
  }

  .apps__inner {
    background-size: cover;
    box-shadow: inset 0 0 0 200vw rgba(249, 250, 255, 0.768627451);
    justify-content: center;
  }

  .footer__top {
    grid-template-columns: repeat(3, 200px);
    grid-gap: 30px;
    justify-content: space-around;
    grid-template-areas: "form address address" "list-one list-two list-three";
  }

  .footer__inner {
    grid-area: form;
  }

  .footer__list-one {
    grid-area: list-one;
  }

  .footer__list-two {
    grid-area: list-two;
  }

  .footer__list-three {
    grid-area: list-three;
  }

  .address {
    grid-area: address;
  }
}

@media (max-width: 970px) {
  .hero__inner {
    background-position: center;
    height: 500px;
    box-shadow: inset 0 0 0 200vw rgba(249, 250, 255, 0.8509803922);
  }

  .hero__content {
    margin: auto;
    left: 0;
    right: 0;
  }

  .popular-food {
    grid-template-columns: repeat(3, 1fr);
  }

  .delivery__inner {
    box-shadow: inset 0 0 0 200vw rgba(249, 250, 255, 0.8509803922);
