  }

  .hero__title {
    font-size: calc(40px + 44.2 * ((100vw - 375px) / 1200));
  }

  .title {
    font-size: calc(24px + 20.4 * ((100vw - 375px) / 1200));
  }

  .popular,
  .restaurant,
  .apps {
    padding: 60px 0;
  }

  .header {
    padding-top: 14px;
  }

  .menu {
    display: none;
  }

  .burger {
    display: initial;
  }

  .hero {
    padding-top: 90px;
  }

  .popular__filter {
    margin-bottom: 23px;
  }

  .popular-food {
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 33px;
  }

  .delivery__item {
    font-size: calc(18px + 10.2 * ((100vw - 375px) / 1200));
  }

  .reviews-slider__item {
    flex-direction: column;
  }

  .reviews-slider__photo {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .reviews-slider .slick-dots {
    display: none;
    height: 0;
  }

  .reviews-slider .slick-dots li {
    display: none;
  }

  .reviews-slider .slick-arrow__prev {
    left: 30%;
    top: 23%;
  }

  .reviews-slider .slick-arrow__next {
    right: 30%;
    top: 23%;
  }

  .apps__text {
    font-size: calc(14px + 10.2 * ((100vw - 375px) / 1200));
  }
}

